<template>
  <div>
    <Heads :title="languages.a12[langIndex]"></Heads>
    <main>
      <section class="en_password">
        <van-field
          class="login_input"
          v-model="en_password"
          type="password"
          :placeholder="languages.a10[langIndex]"
          :error-message="error"
          input-align="center"
        />
        <div class="password_btn">
          <van-button @click="verifyPassword()" color="#dc0000">{{languages.a8[langIndex]}}</van-button>
        </div>
      </section>
    </main>
  </div>
</template>
<style>
.login_input .van-field__control {
  border: 0.01rem solid black;
  width: 5.4rem;
  height: 1rem;
  font-size: 0.4rem;
}
</style>
<style scoped>
main {
  margin-top: 5rem;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.en_password {
  width: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.password_btn {
  margin-top: 1.4rem;
}
</style>

<script>
import Heads from "@/components/Head";

export default {
  name: "Login",
  components: { Heads },
  data() {
    return {
      en_password: "", // 用户进行密码输入
      error: "" // 输入密码错误提醒
    };
  },
  methods: {
    verifyPassword() {
      if (this.en_password == "123456") {
        sessionStorage.setItem("token", this.en_password);
        this.$router.push({ name: "Configure" });
      } else {
        this.error = this.languages.a11[this.langIndex];
      }
    }
  },
  computed: {
    languages() {
      return this.$store.state.languages;
    },
    langIndex() {
      return this.$store.state.langIndex;
    }
  }
};
</script>